import styled from 'styled-components';
import React from 'react';
import {useTags} from '../../hooks/useTags';
// import {createId} from '../../lib/createid';


const Wrapper = styled.section`
  background: #FFFFFF; padding: 12px 16px;
  flex-grow: 1; display:flex; flex-direction: column;
  justify-content: flex-end; align-items: flex-start;
  > ol { margin: 0 -12px;
    > li{
       background: #D9D9D9; border-radius: 18px;
       display:inline-block; padding: 3px 18px; 
       font-size: 14px; margin: 8px 12px;
       &.selected {
        background: red;
       }
    }
  }
  > button{
    background:none; border: none; padding: 2px 4px;
    border-bottom: 1px solid #333; color: #666;
    margin-top: 8px;
  }
`;
type Props = {
  value: number[],
  onChange: (value: number[]) => void
};
const TagsSection: React.FC<Props> = (props) => {

  const {tags, addTag} = useTags();
  // console.log('use tags');


  const selectedTagIds = props.value;

  const onToggle = (tag: number) => {
    const index = selectedTagIds.indexOf(tag);
    if (index >= 0) {
      props.onChange(selectedTagIds.filter(t => t !== tag));
    } else {
      props.onChange([...selectedTagIds, tag]);
    }

  };

  const getClass = (tagIds: number) => selectedTagIds.indexOf(tagIds) >= 0 ? 'selected' : '';
  return (
    <Wrapper>
      <ol>
        {tags.map(tag =>
          <li key={tag.id}
              className={getClass(tag.id)}
              onClick={() => onToggle(tag.id)}
          >{tag.name}</li>
        )}
      </ol>
      {/*<button onClick={() => addTag()}>新增标签</button>*/}
      <button onClick={addTag}>新增标签</button>
    </Wrapper>


  );
};

export {TagsSection};

